* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: aquamarine;
}

.App {
  text-align: center;
  background: linear-gradient(deeppink, pink);
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 20px;
  box-sizing: border-box;
}

.App h1 {
  color: white;
  margin: 20px;
}

.user_block {
  display: flex;
  max-width: 900px;
  padding: 10px;
  background: aquamarine;
  margin: 10px auto;
  border-radius: 20px;
  justify-content: space-around;
}

.todos_block {
  display: flex;
  flex-direction: column;
  margin: 10px;
  border: 1px solid brown;
  padding: 10px;
}

.user_todo_block {
  display: flex;
  text-align: left;
}

.user_todo_block input {
  margin: 0 10px;
}

.user_info {
  display: flex;
  flex-direction: column;
}

.user_location {
  margin: 20px 0;
}

.load_button {
  padding: 20px 35px;
  background: hotpink;
  margin: 0 auto;
  display: block;
  border: none;
  margin-top: 200px;
  color: white;
  font-size: 20px;
  cursor: pointer;
}

.sort_button {
  padding: 20px 35px;
  background: plum;
  margin: 0 auto;
  display: block;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
}

.arrow {
  width: 15px;
  height: 15px;
  margin-left: 10px;
}

.arrow_desc {
  transform: rotate(180deg);
}
.buttonSortDone{
  padding: 10px 20px;
  background: palegreen;
  font-size: 18px;
  color: black;

}
